<template>
    <div id="mytable">
       <el-pagination
          fit="false"
          size="mini"
         @current-change="handleCurrentChange"
         :current-page.sync="currentPage1"
         :page-size="5"
         layout="total, prev, pager, next"
         :total="this.allData.length">
        </el-pagination>

      
      <el-table
        :data="allData.slice((currentPage1-1)*5,currentPage1*5)"
        stripe
        :border="true"
        height="550"
        style="text-align:left;">
        <el-table-column
          prop="googsid"
          label="id"
          width="50">
            <template slot-scope="scope">
                     <h5>{{scope.row.googsid}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="goodsname"
          label="名称"
          width="80">
           <template slot-scope="scope">
                       <h5>{{scope.row.goodsname}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googsisnew"
          label="新品"
          width="50">
           <template slot-scope="scope">
                     <h5>{{scope.row.googsisnew==true?'是':'否'}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googshot"
          label="热卖"
          width="50">
           <template slot-scope="scope">
                     <h5>{{scope.row.googshot==true?'是':'否'}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googsintrod"
          label="介绍"
          width="230">
           <template slot-scope="scope">
                      <el-tooltip placement="top">
                        <div  slot="content"> <div id="goodsint">{{scope.row.googsintrod}}</div></div>
                         <el-button>{{scope.row.googsintrod}}</el-button>
                      </el-tooltip>
            </template>
        </el-table-column>
        <el-table-column
          prop="googstype"
          label="商品类型"
          width="85">
           <template slot-scope="scope">
                       <h5>{{scope.row. googstype}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googsprice"
          label="商品价格"
          width="50">
           <template slot-scope="scope">
                       <h5>{{scope.row.googsprice}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googscount"
          label="商品数量"
          width="50">
           <template slot-scope="scope">
                       <h5>{{scope.row.googscount}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googsloadtime"
          label="上架时间"
          width="80">
           <template slot-scope="scope">
                       <h5>{{scope.row.googsloadtime}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="googspic1base64"
          label="图片"
          width="80">
           <template slot-scope="scope">
                        <img :src="scope.row.googspic1base64" width="40" height="40" class="head_pic"/>
            </template>
        </el-table-column>
        <el-table-column
          prop="token"
          label="操作"
          width="145">
             <template slot-scope="scope">
                      <el-button type="danger"  @click="handremake(scope.row)" size='small'>修改此条商品信息</el-button>
             </template>
        </el-table-column>
        <el-table-column
          prop="token"
          label="操作">
             <template slot-scope="scope">
                      <el-button type="danger"  @click="handdelectde(scope.row.googsid)" size='small'>删除该商品</el-button>
             </template>
        </el-table-column>
      </el-table>
    </div>  
</template>

<script>
import axios from 'axios';
import { mapGetters, mapActions} from 'vuex'
  export default {
    data() {
      return {
        currentPage1: 1,
        allData: [],
      }
    },
    computed:{
      ...mapGetters([
      'getlocalapi',
    ])},
    methods:{
      handleCurrentChange(val) {
        // console.log(`当前页: ${val}`);
      },
      handremake(singlegoods){

        this.$store.dispatch('resetProducts',singlegoods)
        this.$router.push({path:'/memus/changesinglegoodsinfo'})
      },
      handdelectde(x) {
         console.log(x)
        var postdata={"mutationType":"delectonegoods","goodsid":x}
        
          axios.post(this.getlocalapi+"/goods",JSON.stringify(postdata)) 
          .then(response => {
            if(response.data.code=="ok"){
             this.getdata()
              const h = this.$createElement;
              this.$message({
                message: h('p', null, [
                  h('span', null, '删除成功 '),
                  h('i', { style: 'color: teal' }, '')
                ])
              });
              
            }
          })
          .catch(err => {
           console.log(err);
          }) 
       
      },
      getdata(){
       Date.prototype.pattern=function(fmt) {
         var o = {
         "M+" : this.getMonth()+1, //月份
         "d+" : this.getDate(), //日
         "h+" : this.getHours()%12 == 0 ? 12 : this.getHours()%12, //小时
         "H+" : this.getHours(), //小时
          "m+" : this.getMinutes(), //分
          "s+" : this.getSeconds(), //秒
           "q+" : Math.floor((this.getMonth()+3)/3), //季度
          "S" : this.getMilliseconds() //毫秒
        };
         var week = {
           "0" : "/u65e5",
           "1" : "/u4e00",
           "2" : "/u4e8c",
           "3" : "/u4e09",
           "4" : "/u56db",
           "5" : "/u4e94",
           "6" : "/u516d"
          };
          if(/(y+)/.test(fmt)){
              fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length));
          }
            if(/(E+)/.test(fmt)){
                fmt=fmt.replace(RegExp.$1, ((RegExp.$1.length>1) ? (RegExp.$1.length>2 ? "/u661f/u671f" : "/u5468") : "")+week[this.getDay()+""]);
            }
           for(var k in o){
             if(new RegExp("("+ k +")").test(fmt)){
             fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
            }
           }
          return fmt;
        }
        var postdata={"mutationType":"getallgoods"}
        axios.post(this.getlocalapi+"/goods",JSON.stringify(postdata)) 
        .then(response => {
             var iarr=[]
             for(var i=0;i<response.data.length;i++){
                var t=response.data[i].googsloadtime
                t= new Date(t).pattern("yyyy-MM-dd hh:mm:ss")
                response.data[i].googsloadtime=t
                iarr.push(response.data[i])

             }
             this.allData=iarr
        })
       .catch(err => {
           console.log(err);
        })   
      }
    },
    created() {
      this.getdata()
    },
  }
</script>
<style scoped>
#mytable{
  height:5rem;
  width:35rem;
  margin-top:1.5rem;
}
el-table{
  text-align:center;
}
#goodsint{
  width:10rem;
  color:red;
  overflow:hidden;
  margin:auto;
}
</style>


